.wrap-logo {
    position: relative;
}

.wrap-logo::before, .wrap-logo::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    border: 2px solid #f0d721;
    transition: all 0.25s ease-out;
}

.wrap-logo::before {
    background-color: #f0d721;
    top: -5px;
    left: -5px;
}

.wrap-logo::after {
    bottom: -5px;
    right: -5px;
}

.wrap-logo:hover::before {
    top: 5px;
    left: 5px;
}

.wrap-logo:hover::after {
    bottom: 5px;
    right: 5px;
}

