.basic-slide {
    display: inline-block;
    width: 215px;
    padding: 10px 0 10px 15px;
    font-weight: 400;
    color: #377D6A;
    background: #efefef;
    border: 0;
    border-radius: 3px;
    outline: 0;
    text-indent: 70px;
    transition: all .3s ease-in-out;
}

.basic-slide::-webkit-input-placeholder {
    color: #efefef;
    text-indent: 0;
    font-weight: 300;
}

.basic-slide+label {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    padding: 8px 4px;
    text-shadow: 0 1px 0 rgba(19, 74, 70, 0.4);
    background: #f0d721;
    color: #444540;
    transition: all .3s ease-in-out;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

.basic-slide:focus, .basic-slide:active {
    color: #f0d721;
    text-indent: 0;
    background: #fff;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.basic-slide:focus::-webkit-input-placeholder, .basic-slide:active::-webkit-input-placeholder {
    color: #aaa;
}

.basic-slide:focus+label, .basic-slide:active+label {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    
}

